export default {
  data() {
    return {
      codemirror: null,
      codeMirrorDoc: null,
      selectedText: '',
      startPos: '',
      endPos: '',
      markList: [],
      quickMarkVis: false,
      quickMarkX: 0,
      quickMarkY: 0,
      showFlag: true,
      fontSize: '14',
    };
  },
  mounted() {
    this.codemirror = this.$refs.mycode.codemirror;
    this.codeMirrorDoc = this.codemirror.getDoc();
    this.codemirror.on('cursorActivity', (cm) => {
      //拖动鼠标开始位置结束位置，支持正反选
      this.selectedText = cm.getSelection();
      this.startPos = cm.getCursor('start');
      this.endPos = cm.getCursor('end');
    });
    // document.body.appendChild(this.$refs.quickMark.$el);
    document.body.addEventListener('click', () => {
      this.quickMarkVis = false;
    });
    document.querySelector('.vue-codemirror').addEventListener('click', (e) => {
      e.stopPropagation();
    });
    document
      .querySelector('.vue-codemirror')
      .addEventListener('mouseup', (e) => {
        if (this.selectedText) {
          this.quickMarkX = e.pageX;
          this.quickMarkY = e.pageY;
          this.quickMarkVis = true;
        } else {
          this.quickMarkVis = false;
        }
      });
  },
  methods: {
    // 将位置转换为数字
    transformPosToIndex(pos) {
      return this.codeMirrorDoc.indexFromPos(pos);
    },
    // 将数字转换为位置
    transformIndexToPos(index) {
      return this.codeMirrorDoc.posFromIndex(index);
    },
    // 清除选中区域
    clearSelection() {
      this.selectedText = '';
      return this.codeMirrorDoc.undoSelection();
    },
    // 清除所有的文字标记
    clearAllMarkText() {
      this.codeMirrorDoc.getAllMarks().forEach((item) => {
        item.clear();
      });
    },
    // 标记文字
    markText() {
      this.onceMarkCode = this.selectedText;
      this.clearSelection();
      this.clearAllMarkText();
      this.markList.forEach((item) => {
        const start = this.transformIndexToPos(item.start_pos);
        const end = this.transformIndexToPos(item.end_pos);
        this.codemirror.markText(start, end, {
          css: 'background:' + item.bg_color + ';color:' + item.txt_color,
        });
      });
      this.quickMarkVis = false;
    },
    // 清除选中区域的文字标记
    clearMarkText() {
      const startIndex = this.transformPosToIndex(this.startPos);
      const endIndex = this.transformPosToIndex(this.endPos);
      const newMarkList = this.markList.filter(
        (item) => !(startIndex <= item.start_pos && item.end_pos <= endIndex)
      );
      this.markList = newMarkList;
      this.markText();
    },
    // 点击按钮进行标记文字
    doMarkText(subCategory) {
      this.markList.push({
        //按约定组织好集合的结构
        entity_mention: this.selectedText,
        start_pos: this.transformPosToIndex(this.startPos),
        end_pos: this.transformPosToIndex(this.endPos),
        entity_type: subCategory.labelName,
        result_state: 2,
        result_type: 1,
        // result_state_note : _this.result_state_id2des_dict[2],
        // result_type_note : _this.result_type_id2des_dict[1],
        bg_color: subCategory.bg_color,
        txt_color: subCategory.txt_color,
      });
      this.markText();
      this.$emit('addMarkItem', this.markList);
    },
  },
};
