<template>
  <div class="quick-mark" v-show="visible" ref="quickMark">
    <!-- <div class="edit-icon"></div> -->
    <ul class="mark-list">
      <li
        :class="['mark-item', { active: activeKey === key }]"
        v-for="(item, key) in allMarkList"
        :key="key"
        @click="handleClick($event, key)"
      >
        <div class="category">{{ key }}<i class="el-icon-arrow-right"></i></div>
        <ul class="submark-list" v-show="activeKey === key">
          <li
            class="mark-item"
            v-for="item in allMarkList[activeKey]"
            :key="item.labelName"
            :style="{
              'background-color': item.bg_color,
            }"
            @click="quickMarkText($event, item)"
          >
            {{ item.labelName }}
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: ['allMarkList', 'visible', 'quickMarkX', 'quickMarkY'],
  data() {
    return {
      activeKey: '',
    };
  },
  methods: {
    handleClick(e, key) {
      e.stopPropagation();
      this.activeKey = key;
    },
    quickMarkText(e, item) {
      e.stopPropagation();
      this.activeKey = '';
      this.$emit('quickMarkText', item);
    },
  },
  watch: {
    quickMarkX() {
      this.$refs.quickMark.style.left = this.quickMarkX + 'px';
      this.$refs.quickMark.style.top = this.quickMarkY + 'px';
    },
  },
};
</script>

